body {
  font-family: Roboto, sans-serif;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header-container {
  background-color: #4c73c8;
  padding: 20px 0;
}

.main-container {
  padding: 10px 20px;
}

.search-form {
  justify-content: center;
  display: flex;
}

.input-form {
  border-radius: 10px;
  padding: 10px;
  box-shadow: 0 20px 30px -10px #26394d;
}

.input-form::-ms-input-placeholder {
  font-size: 15px;
}

.input-form::placeholder {
  font-size: 15px;
}

.input-wrapper {
  position: relative;
}

.search-btn {
  width: 37px;
  height: 36px;
  z-index: 3;
  color: #3f4656;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 50%;
  right: 2px;
  transform: translate(0, -50%);
}

.search-btn:hover, .search-btn:focus {
  background-color: #d9dee3;
}

.search-icon {
  fill: currentColor;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  row-gap: 20px;
  margin-bottom: 15px;
  display: flex;
}

.photo-card {
  width: calc(25% - 7.5px);
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.info {
  text-align: center;
  justify-content: space-between;
  padding: 20px 7px;
  display: flex;
}

.info-item {
  flex-direction: column;
  gap: 4px;
  font-size: 12px;
  display: flex;
}

.load-more {
  color: #f0f8ff;
  cursor: pointer;
  background-color: #4c73c8;
  border: none;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 600;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.load-more:hover, .load-more:focus {
  background-color: #246cba;
}

